E,gBAAA","sourcesContent":["\n@import '~projuris-acordos-theme/src/common/colors.scss';\n\n.bank-accounts {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n\n  .bank-accounts__container {\n    display: flex;\n    flex-direction: column;\n    gap: 6px;\n\n    .bank-accounts__container-account {\n      margin: 0;\n      display: flex;\n      padding: 6px 3px 6px 18px;\n      border-radius: 4px;\n      transition: .2s ease-out all;\n      cursor: pointer;\n      position: relative;\n\n      .bank-accounts__container-inner {\n        padding-left: 6px;\n      }\n\n      .bank-accounts__account-info {\n        line-height: normal;\n        font-size: 13px;\n        color: $--color-text-secondary;\n      }\n\n      .bank-accounts__account-icons {\n        position: absolute;\n        opacity: 0;\n        right: 0;\n        top: 0;\n        margin: 6px 6px 0 0;\n        padding-left: 30px;\n        transition: .2s ease-out all;\n\n        .bank-accounts__account-icon {\n          cursor: pointer;\n          margin-left: 3px;\n          transition: .2s ease-out all;\n\n          &:hover {\n            color: $--color-primary;\n\n            &.el-icon-delete {\n              color: $--color-danger;\n            }\n          }\n        }\n      }\n\n      &:not(.is-checked) .bank-accounts__container-inner .bank-accounts__account-info-title img {\n        display: none;\n      }\n\n      &:not(.is-not-checkable):hover {\n        background-color: $--color-light-gray;\n        .bank-accounts__container-inner { opacity: .85; }\n        .bank-accounts__account-icons { opacity: 1; }\n      }\n\n      &.is-checked {\n        background-color: $--color-light-gray;\n\n        .bank-accounts__container-inner {\n          border-top-left-radius: 0px;\n          border-bottom-left-radius: 0px;\n\n          .bank-accounts__account-info-title {\n            font-weight: 600;\n          }\n        }\n      }\n\n      &.is-not-checked {\n        .bank-accounts__container-inner {\n          border-top-left-radius: 0px;\n          border-bottom-left-radius: 0px;\n        }\n      }\n\n      &.is-not-checkable {\n        cursor: not-allowed;\n\n        .bank-accounts__container-inner {\n          .bank-accounts__account-info-title {\n            color: $--color-text-secondary;\n          }\n        }\n      }\n    }\n  }\n\n  .bank-accounts__link {\n    margin-left: 18px\n  }\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj-color-white-blue: #F0F9FE !default;\n$--pj-color-disabled-blue: #9ad2f9 !default;\n\n$-pg-color-overlay: #19415F !default;\n\n$--pj-color-red: #CC4148 !default;\n$--pj-color-light-red: #EC767C !default;\n$--pj-color-disable-red: #FFF2F3 !default;\n\n$--color-primary: $--pj-color-blue !default;\n$--color-primary-light-1: mix($--color-white, $--color-primary, 10%) !default;\n$--color-primary-light-2: mix($--color-white, $--color-primary, 20%) !default;\n$--color-primary-light-3: mix($--color-white, $--color-primary, 30%) !default;\n$--color-primary-light-4: mix($--color-white, $--color-primary, 40%) !default;\n$--color-primary-light-5: mix($--color-white, $--color-primary, 50%) !default;\n$--color-primary-light-6: mix($--color-white, $--color-primary, 60%) !default;\n$--color-primary-light-7: mix($--color-white, $--color-primary, 70%) !default;\n$--color-primary-light-8: mix($--color-white, $--color-primary, 80%) !default;\n$--color-primary-light-9: mix($--color-white, $--color-primary, 90%) !default;\n\n$--color-secondary: $--pj-color-light-blue !default;\n$--color-secondary-light-1: mix($--color-white, $--color-secondary, 10%) !default;\n$--color-secondary-light-2: mix($--color-white, $--color-secondary, 20%) !default;\n$--color-secondary-light-3: mix($--color-white, $--color-secondary, 30%) !default;\n$--color-secondary-light-4: mix($--color-white, $--color-secondary, 40%) !default;\n$--color-secondary-light-5: mix($--color-white, $--color-secondary, 50%) !default;\n$--color-secondary-light-6: mix($--color-white, $--color-secondary, 60%) !default;\n$--color-secondary-light-7: mix($--color-white, $--color-secondary, 70%) !default;\n$--color-secondary-light-8: mix($--color-white, $--color-secondary, 80%) !default;\n$--color-secondary-light-9: mix($--color-white, $--color-secondary, 90%) !default;\n\n$--color-primary-dark: $--pj-color-dark-blue !default;\n$--color-success: #14CC30 !default;\n$--color-warning: #FFC900 !default;\n$--color-danger: #FF4B54 !default;\n$--color-info: #2FBBEC !default;\n\n$--color-success-light-1: mix($--color-white, $--color-success, 10%) !default;\n$--color-success-light-5: mix($--color-white, $--color-success, 50%) !default;\n$--color-warning-light-5: mix($--color-white, $--color-warning, 50%) !default;\n$--color-danger-light-5: mix($--color-white, $--color-danger, 50%) !default;\n$--color-danger-light-3: mix($--color-white, $--color-danger, 30%) !default;\n$--color-info-light-5: mix($--color-white, $--color-info, 50%) !default;\n\n$--color-success-light: mix($--color-white, $--color-success, 80%) !default;\n$--color-warning-light: mix($--color-white, $--color-warning, 80%) !default;\n$--color-danger-light: mix($--color-white, $--color-danger, 80%) !default;\n$--color-info-light: mix($--color-white, $--color-info, 80%) !default;\n\n$--color-success-lighter: mix($--color-white, $--color-success, 90%) !default;\n$--color-warning-lighter: mix($--color-white, $--color-warning, 90%) !default;\n$--color-danger-lighter: mix($--color-white, $--color-danger, 90%) !default;\n$--color-info-lighter: mix(